*,
*::before, *::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

img, a {
  display: block; }

.main-container {
  position: relative;
  min-height: 100vh; }

.contact {
  background-color: #fafafa;
  height: auto; }

.contact::before {
  background-color: white;
  height: 90px;
  /* fixed header height*/
  margin: -90px 0 0;
  /* negative fixed header height */ }

.contact .wrapper {
  height: auto;
  width: auto;
  max-width: 1570px;
  padding-top: 30px; }

.contact .wrapper .container:nth-child(2) {
  flex-direction: column;
  align-items: flex-start; }
  .contact .wrapper .container:nth-child(2) h1 {
    font-size: 54px;
    font-weight: 800;
    letter-spacing: -3px; }
  .contact .wrapper .container:nth-child(2) h1:first-child {
    color: #2C4755; }
  .contact .wrapper .container:nth-child(2) h1:nth-child(2) {
    color: #10CAB7;
    font-size: 50px;
    font-weight: 800;
    letter-spacing: -3px;
    margin-left: 40px; }

.contact .social {
  justify-content: left;
  margin-bottom: 150px;
  margin-top: 20px; }

.contact .social p {
  letter-spacing: -1px;
  font-size: 15px;
  margin-right: 30px;
  margin-left: 100px; }

.contact a {
  letter-spacing: normal;
  margin-right: 20px;
  font-size: 20px;
  color: #2C4755; }

.contact a:last-child {
  margin: 0; }

.contact .section-title h1 {
  color: #ebeced; }

@media (max-width: 1260px) {
  .contact .wrapper .container:nth-child(2) {
    flex-direction: column;
    align-items: center; }
    .contact .wrapper .container:nth-child(2) h1 {
      font-size: 54px;
      font-weight: 800;
      letter-spacing: -3px;
      text-align: center; }
    .contact .wrapper .container:nth-child(2) h1:nth-child(2) {
      font-size: 50px;
      font-weight: 800;
      letter-spacing: -3px;
      margin-left: 0; }

  .contact .social p {
    letter-spacing: -1px;
    font-size: 15px;
    margin-right: 30px;
    margin-left: 0; }

  .contact .social {
    justify-content: center; } }
@media (max-width: 650px) {
  .contact .wrapper {
    width: 90%; }

  .contact .wrapper .container:nth-child(2) h1 {
    font-size: 34px;
    letter-spacing: -1px; }
  .contact .wrapper .container:nth-child(2) h1:nth-child(2) {
    font-size: 30px;
    letter-spacing: -1px; }

  .contact .social {
    margin-bottom: 50px; }

  .contact .social p {
    display: none; } }
@media (max-width: 420px) {
  .contact .wrapper .container:nth-child(2) h1 {
    font-size: 30px; }
  .contact .wrapper .container:nth-child(2) h1:nth-child(2) {
    font-size: 26px; }

  .contact .social {
    margin-bottom: 25px; }

  .contact .social p {
    display: none; } }
@media (max-width: 310px) {
  .contact .wrapper .container:nth-child(2) h1:nth-child(2) {
    font-size: 23px; }
    .contact .wrapper .container:nth-child(2) h1:nth-child(2) h1 {
      font-size: 27px; } }
